.alert p {
    margin-bottom: 0!important;
}
body {
    background: #d53369;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #cbad6d, #d53369);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #cbad6d, #d53369); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
header {
    margin: 30px 0;
}
.content {
    padding:20px;
}
.content-main {
    background-color: white;
    padding: 30px;
    border-radius: 20px;
}
h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    color: white;
    text-align: center;
    font-size: 4.8rem!important;
}
h2 {
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    color:#004e92;
    margin-bottom:30px;
    font-size:3rem!important;
    text-align: center;
}
.field {
    margin-bottom: 2rem;
}
.error {
    text-align: center;
}
.alert {
    position: relative;
    padding: .75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem;
}
.alert-primary {
    color: #11406E;
    background-color: #cce5ff;
    border-color: #b8daff;
}
.alert-success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}
.alert-danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}
.alert-warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba;
}
.expenses-incurred  {
    margin-bottom: 2rem;
}
.expenses-incurred li{
    list-style: none;
    margin-bottom: 0rem;
    border-bottom: 1px solid #e1e1e1;
    padding: 1rem 0;
}
.expenses-incurred li:last-of-type {
    margin-bottom: 0!important;
}
.expenses-incurred li p {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:0;
}
.expenses-incurred li p .expense {
    background-color: #B22454;
    padding: 1rem;
    color:white;
}
